import React from "react"
import Layout from "../components/layout"
import { useStaticQuery, graphql } from "gatsby"

const indexPage = () => {
  const {data} = useStaticQuery(
    graphql`
    query {
      allMarkdownRemark {
        edges {
          node {
            frontmatter {
              projectName
              projectDate
            }
          }
        }
      }
    }
  `)

  console.log(data)
  return (
    <Layout>
      <div> index </div>
    </Layout>
  )
}

export default indexPage